import { Box, Slider } from "@mui/material";
import styles from "./menu.module.css";
import { useState, useEffect } from "react";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { Stage, Layer, Rect, Circle } from "react-konva";
import { useSelector, useDispatch } from "react-redux";
import { setScroll } from "../../redux/pageSlice";

function Scroll() {
  const [wheel, setWheel] = useState(0);
  const dispatch = useDispatch();

  window.addEventListener("wheel", (evt) => {
    +evt.deltaY > 0 ? setWheel(wheel + 1) : setWheel(wheel > 0 ? wheel - 1 : 0);
  });

  if (!(wheel % 25)) dispatch(setScroll(wheel));

  return (
    <>
      {/* <Stage width={window.innerWidth} height={300}>
        <Layer>
          {tabs.map((tab) => {
            return (
              <Circle
                x={Math.random() * window.innerWidth}
                y={100}
                stroke="red"
                radius={50}
                onClick={() => handleClick(tab.name)}
              />
            );
          })}
        </Layer>
      </Stage> */}
      {/* {page2} */}
      <Box className={styles.menu}>
        {wheel}
        {/* {tabs.map((tab) => {
          return (
            <Box
              className={styles.button}
              sx={
                page === tab.name
                  ? { color: "gray", transition: "500ms" }
                  : { color: "black", transition: "500ms" }
              }
              key={tab.name}
              onClick={() => handleClick(tab.name)}
            >
              {tab.name}
            </Box>
          );
        })} */}
      </Box>
    </>
  );
}

export default Scroll;
